    max-width: 1024px;
    margin: 0 auto;
}

#wrap.mobile #main {
    position: absolute;
    top: 110px;
    width: 100%;
    max-width: 1024px;
}




/******** web page***********/
#Sec_web {
    width: 100%;
    height: 500px;
    background: #ececec;
}



#Sec0_web .sect-inner {
    width: 80%;
    position: relative;
    margin: 0 auto;
    }
    
#Sec0_web .sect-inner .sect-title{
    text-align: center;
    font-size: 78px;
    font-family: "Bebas";
    font-weight: bold;
    padding-bottom: 20px;
    padding-top: 80px;
    color: #acacac;
    }

#Sec0_web .sect-inner .sect-content .screen-wrap{
    position: relative;
    width: 100%;
    height: 100%;
}
#Sec0_web .sect-inner .sect-content{
    height: 700px;
    position: relative;
}

#Sec0_web .sect-inner .sect-content .screen-wrap .screen{
    position: absolute;   
}

#Sec0_web .sect-inner .sect-content .screen-wrap .screen.pc{
    width: 60%; right: 0; bottom: 0;
    
}
#Sec0_web .sect-inner .sect-content .screen-wrap .screen.tablet{
    width: 40%; right: -8%; bottom: 0;
    
}
#Sec0_web .sect-inner .sect-content .screen-wrap .screen.mobile{
    width: 108px; right: -10%; bottom: 0;
    
}


#Sec0_web .sect-inner .sect-content .work-text{
    position: absolute; top: 17%; left: 0; width: 70%;
    
}
#Sec0_web .sect-inner .sect-content .work-text h4{
    margin-bottom: 12px;
}
#Sec0_web .sect-inner .sect-content .work-text .desc{
    margin-bottom: 12px;
}

@media screen and (max-width:950px){
    
   
}


@media screen and (max-width:768px){
    #Sec0_web .sect-inner .sect-content .work-text{width: 80%;}
   
    #Sec0_web .sect-inner .sect-content .screen-wrap .screen.pc{width: 70%;}
   
    #Sec0_web .sect-inner .sect-content .screen-wrap .screen.tablet{width: 55%;}
    
}

@media screen and (max-width:600px){

    #Sec0_web .sect-inner .sect-content .work-text{top: 10%;}
    #Sec0_web .sect-inner .sect-content .screen-wrap .screen.pc{width: 80%;}
   
    #Sec0_web .sect-inner .sect-content .screen-wrap .screen.tablet{width: 64%;}
   
    #Sec0_web .sect-inner .sect-content .screen-wrap .screen.mobile{width: 80px;}
    
    
}

@media screen and (max-width:480px){
  
    #Sec0_web .sect-inner{margin: 0 16px; width: calc(100% - 32px);}
    #Sec0_web .sect-inner .sect-content{height: 620px; text-align: center;}
    #Sec0_web .sect-inner .sect-content .work-text {width: 100%;}
    #Sec0_web .sect-inner .sect-content .work-text .desc{font-size: 14px;}
    #Sec0_web .sect-inner .sect-content .screen-wrap .screen img{width: 100%;}
    #Sec0_web .sect-inner .sect-content .screen-wrap .screen.pc{right: 10%;}
    #Sec0_web .sect-inner .sect-content .screen-wrap .screen.tablet{right: 0;}
    #Sec0_web .sect-inner .sect-content .screen-wrap .screen.mobile{right: 0;}
   

}